.rbc-event {
  border: none;
  box-sizing: border-box;
  box-shadow: none;
  margin: 0;
  padding: 2px 5px;
  background-color: #fff;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
  text-align: center;
}

rbc-event.rbc-selected {
  background-color: #1d366d;
  color: #fff;
}

.driverCalendar {
  position: relative;
  margin: 16px 10vh;
  background: #1d366d;
  border-radius: 8px;
}

.tripCard {
  position: relative;
  margin: 16px 10vh;
  border-radius: 8px;
}

