  height: 50px;
  position: relative;
}

.rbc-header > span {
  font-family: Bai Jamjuree;
  font-size: 18px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.swConfirm {
  font-family: "Bai Jamjuree";
  background-color: #2cc84d;
  color: #fff;
  border-radius: 10px;
  padding: 4px 16px;
  border: 0;
