}

.hoverDriver {
  position: absolute;
  top: 16px;
  width: 240;
  height: 0px;
  left: 50%;
  background-color: transparent;
  transition: all 0.3s ease;
  z-index: -1;
  transform: translateX(-50%);
}

.person:hover > .hoverDriver {
  text-align: center;
  position: absolute;
  right: 50%;
  transform: translateX(-50%);
  width: 240px;
  height: 240px;
  background-color: rgba(29, 54, 109, 0.6);
  z-index: 2;
}

.hoverCar {
  position: absolute;
  top: 24px;
  width: 184px;
  height: 0px;
  left: 50%;
  background-color: transparent;
  transition: all 0.3s ease;
  z-index: -1;
  transform: translateX(-50%);
}

.person:hover > .hoverCar {
  text-align: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 184px;
  height: 184px;
  background-color: rgba(29, 54, 109, 0.6);
  z-index: 2;
}

.editDriver {
  position: absolute;
  top: 24px;
  width: 240;
  height: 0px;
  left: 50%;
  background-color: transparent;
  transition: all 0.3s ease;
  border-radius: 50%;
  z-index: -1;
  transform: translateX(-50%);
}

.person:hover > .editDriver {
  text-align: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 154px;
  height: 154px;
  background-color: rgba(29, 54, 109, 0.6);
  z-index: 2;
}

