.ScrollCar {
  /* margin-top : 20px; */
  width: 100%;
  height: calc(90vh - 72px);
  /* overflow: auto; */
}

.sideFilter.isactive {
  width: 30vw;
  transition: 0.3s;
  transition-timing-function: ease-in-out;
}

.contentFilter {
  z-index: 90;
  position: fixed;
  top: -64px;
  /* right: 30vw ; */
  width: 100vw;
  height: calc(100vh + 64px);
  background-color: rgba(1, 1, 1, 0.2);
  z-index: 4;
}

/* .ant-layout:has(.ant-layout-content.site-layout-background>.sideFilter.isactive){
    background-color: red;
} */

.sideFilter {
  background-color: #fff;
  position: fixed;
  top: -72px;
  right: 0;
  height: calc(100vh + 72px);
  width: 0;
  transition: 0.3s;
  transition-timing-function: ease-in-out;
  overflow-x: hidden;
  z-index: 5;
}

/* mobile */

@media only screen and (max-width: 600px) {
  .driverCalendar {
    margin: 16px 8px;
  }
  .tripCard {
    margin: 16px 8px;
  }
  .imgCar {
    width: 30vw;
    /* height: 24vh ; */
  }
  /* .driverimg{
        width : 240px ;
        height : 240px ;
        object-fit : cover ;
    } */
  .cardBooking {
    padding: 12px;
    background: #e3e3e3;
    /* border: 0.5px solid black ; */
    border-radius: 24px;
    overflow: inherit;
    width: max-content;
  }
  .jobView {
    margin-left: 0;
    /* background : red; */
    width: 100%;
  }
  .sideFilter.isactive {
    width: 80vw;
    transition: 0.3s;
    transition-timing-function: ease-in-out;
  }
  .carImg {
    position: relative;
  }
  .text {
    text-align: center;
  }
  .selectWidth {
    font-size: 1.5vh;
    width: auto;
  }
  .cardMobile {
    display: inline-block;
    margin-right: 4%;
    /* width : 100%; */
    padding-right: 20px;
  }
  .horizonScroll {
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
  }
  .Scroll {
    margin-top: 20px;
    width: 100%;
    height: 24vh;
    overflow: auto;
  }
  .margin {
    margin-left: 16px;
    margin-right: 16px;
  }
  .padDate {
    padding-left: 16px;
    padding-right: 10px;
    background-color: #fff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    display: flex;
    justify-content: space-between;
  }
  .btn {
    width: auto;
  }
  .font {
    font-size: 1.5vh;
  }
  .carfont {
    font-size: 2vh;
    padding-left: 8px;
  }
  .dragicon1 {
    position: relative;
    right: "0%";
  }
  .dragRequest {
    display: inline-flex;
    margin-right: 20px;
    /* right: '0%'; */
  }
  .carPos {
    position: relative;
  }
  .fontGeneralBtn {
    /* margin-top : 12% ; */
    font-size: 3vw;
    height: auto;
  }
  .posGeneralBtn {
    margin-top: 8px;
    position: relative;
    text-align: center;
  }
  .ScrollCar {
    margin-top: 8px;
    margin-right: 8px;
    width: 100%;
    height: auto;
  }
}

/* tablet or pc */

