    margin-right: 120px;
  }
  .padDate {
    z-index: -5;
    /* margin-top: -16px ; */
    padding-top: 16px;
    padding-left: 120px;
    padding-right: 10px;
    background-color: #fff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    display: flex;
    justify-content: space-between;
  }
  .btn {
    width: 20%;
  }
}

@media only screen and (max-width: 1367px) {
